<template>
  <div class="Landing" :class="['slide-' + animationDetails.currentStep ]" ref="landing">
    <div class="background" :class="{ isOff: !animationDetails.active}">
      <Three :isActive="animationDetails.active" :step="animationDetails.renderStep"></Three>
    </div>
    <div class="background-map"></div>

    <section id="slide-0" :class="{ isActive: animationDetails.currentStep === 0 }">
      <h1>slide {{ animationDetails.currentStep }}</h1>
    </section>

    <section id="slide-1">
      <h1>Welcome to PGS</h1>
      <h2>lorem ipsum dolor sit amet</h2>
    </section>

    <section id="slide-2">
      <h1>hello world - slide 2</h1>
    </section>
    
    <section id="slide-3">
    </section>
    
    <section id="slide-4">
      <h1>hello world - slide 4</h1>
    </section>

    <section id="slide-5">
      <h1>hello world - slide 5</h1>
    </section>
    
    <div class="mouse">
      <div class="scroll-wheel"></div>
    </div>
    <div class="Menu" :class="{ isActive: animationDetails.currentStep > 0 }">
        <a class="logo"><span>PGS Software</span></a>
        <ul>
            <li>About</li>
            <li>Contact</li>
        </ul>
    </div>
  </div>
</template>

<script>
  /* eslint-disable */
  import {
    throttle,
    isFunction
  } from 'lodash';
  
  import Three from './ThreeDebug';

  const scrollSettings = {
    sensivity: 30,
    duration: 600
  };

  let ticking = false;
  let container;

  let animationDetails = {
    active: false,
    animatedSteps: [1, 2, 3],
    animationMap: {
        1: 0,
        2: 1,
        3: 2
    },
    currentStep: 0,
    renderStep: 0,
    totalSlides: 6,
  };

  export default {
    name: 'landing',
    components: {
      Three,
    },
    data() {
      return {
        animationDetails
      };
    },
    mounted() {
      window.addEventListener('wheel', throttle(fullPageScroll, 60), false);
      container = this.$refs.landing;
    },
  };

  function fullPageScroll(e) {
    const delta = e.wheelDelta;

    if (ticking != true) {
      if (delta <= -scrollSettings.sensivity) { //nextItem
        //Down scroll
        ticking = true;
        if (animationDetails.currentStep !== animationDetails.totalSlides - 1) {
          animationDetails.currentStep++;
        }
        scrollItems();
        slideDurationTimeout(scrollSettings.duration);
      }

      if (delta >= scrollSettings.sensivity) { //prevItem
        //Up scroll
        ticking = true;
        if (animationDetails.currentStep !== 0) {
          animationDetails.currentStep--;
        }

        scrollItems();
        slideDurationTimeout(scrollSettings.duration);
      }
    }
  }

  function slideDurationTimeout(slideDuration) {
    setTimeout(function () {
      ticking = false;
    }, slideDuration);
  }

  function mapSlideStepToRender() {

  }

  function scrollItems() {
    animationDetails.active = animationDetails.animatedSteps.reduce((prev, n) => {
        return animationDetails.currentStep === n || prev;
    }, false);

    const mappedStep = animationDetails.animationMap[animationDetails.currentStep];
    if (mappedStep !== undefined) {
        animationDetails.renderStep = mappedStep;
    }
    
  }

  // --- class helpers
  function hasClass(el, className) {
    if (el.classList) {
      return el.classList.contains(className);
    } else {
      return !!el.className.match(new RegExp('(\\s|^)' + className + '(\\s|$)'))
    }
  }

  function addClass(el, className) {
    if (el.classList) {
      el.classList.add(className);
    } else if (!hasClass(el, className)) {
      el.className += " " + className;
    }
  }

  function removeClass(el, className) {
    if (el.classList) {
      el.classList.remove(className);
    } else if (hasClass(el, className)) {
      var reg = new RegExp('(\\s|^)' + className + '(\\s|$)');
      el.className = el.className.replace(reg, ' ');
    }
  }

</script>

<style lang="scss" src="./Landing.scss" scoped></style>


