import Vue from 'vue';
import Router from 'vue-router';
import Landing from '@/components/Landing';
import ThreeDebug from '@/components/ThreeDebug';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Landing',
      component: Landing,
    },
    {
      path: '/debug',
      name: 'ThreeDebug',
      component: ThreeDebug,
      props: {
        isActive: true,
        step: 0,
      },
    },
  ],
});



