const PARTIAL_BASE_WPT = 'wpt';
const PARTIAL_BASE_WROCLAVIA = 'wroclavia';

export const PARTIALS_WPT = [{
    model: `${PARTIAL_BASE_WPT}-buildings.json`,
    edges: true,
    wireframe: true,
    faces: true,
  },
  {
    model: `${PARTIAL_BASE_WPT}-terrain.json`,
    edges: false,
    wireframe: true,
    faces: true,
    position: [0, -0.05, 0],
  },
  {
    model: `${PARTIAL_BASE_WPT}-green.json`,
    edges: true,
    wireframe: false,
    faces: true,
  },
  {
    model: `${PARTIAL_BASE_WPT}-building-alfa.json`,
    edges: true,
    wireframe: true,
    faces: true,
  },
];

export const PARTIALS_WROCLAVIA = [{
    model: `${PARTIAL_BASE_WROCLAVIA}-buildings.json`,
    edges: true,
    wireframe: true,
    faces: true,
  },
  {
    model: `${PARTIAL_BASE_WROCLAVIA}-terrain.json`,
    edges: false,
    wireframe: true,
    faces: true,
  },
];



