import * as THREE from 'three';

import {
  TweenMax,
  Power4,
} from 'gsap';

const PALLETE = {
  blue: 0x3498db,
  green: 0x2ecc71,
  orange: 0xee5624,
  darkOrange: 0xd35400,
  yellow: 0x34495e,
};

const DEFAULTS = {
  noise: 0.8,
  radius: 12,
  radius_outer_factor: 1.2,
  rotate_speed: 0.03,
  scale_speed: 2,
};

export default class PlanetLoader {
  constructor(size) {
    this.isActive = false;
    this.progress = 0;
    this.previous = 0;
    this.scaleFactor = 0;
    this.scale = 1;
    this.size = size || DEFAULTS.radius;
    this.mesh = new THREE.Object3D();

    this.planet = createPlanet(this.size);
    this.wireframe = createWireframe(this.size * DEFAULTS.radius_outer_factor);

    this.mesh.add(this.planet);
    this.mesh.add(this.wireframe);

    this.addLights();
  }

  addLights() {
    const ambientLight = new THREE.AmbientLight(0x663344, 2);
    this.mesh.add(ambientLight);

    const light = new THREE.DirectionalLight(0xffffff, 1.5);
    light.position.set(200, 100, 200);
    // light.castShadow = true;
    light.shadow.camera.left = -400;
    light.shadow.camera.right = 400;
    light.shadow.camera.top = 400;
    light.shadow.camera.bottom = -400;
    light.shadow.camera.near = 1;
    light.shadow.camera.far = 1000;
    light.shadow.mapSize.width = 2048;
    light.shadow.mapSize.height = 2048;

    this.mesh.add(light);
  }

  update() {
    if (this.isActive) {
      // this.updateScale(scale);

      this.planet.rotation.z -= DEFAULTS.rotate_speed / 10;
      this.planet.rotation.y -= DEFAULTS.rotate_speed;

      this.wireframe.rotation.z += DEFAULTS.rotate_speed / 2;
      this.wireframe.rotation.y -= DEFAULTS.rotate_speed / 2;

      this.mesh.scale.set(this.scale, this.scale, this.scale);
    }
  }

  updateScale(t) {
    this.scale = 1 - ((Math.abs(Math.sin(t * DEFAULTS.scale_speed)) / 6) + this.scaleFactor);
  }

  getMesh() {
    return this.mesh;
  }

  clearFaces() {
    const facesLength = this.planet.geometry.faces.length;
    for (let i = 0; i < facesLength; i += 1) {
      this.planet.material.materials[i] = getMaterial(PALLETE.orange, true);
      this.planet.material.materials[i].needsUpdate = true;
    }
  }

  activate() {
    this.isActive = true;
  }

  updateFaces(p) {
    const facesLength = this.planet.geometry.faces.length;
    const curr = Math.min(Math.ceil((facesLength * p) + 1), facesLength);
    // console.log('this.previous', this.previous)
    // console.log('curr', curr)

    for (let i = this.previous; i < curr; i += 1) {
      this.planet.material.materials[i] = getMaterial(PALLETE.orange);
      this.planet.material.materials[i].needsUpdate = true;
    }

    this.previous = Math.max(this.progress - 1, 0);
    this.progress = curr;
  }

  dispose() {
    return new Promise((resolve) => {
      const o = {
        progress: 1,
      };

      TweenMax.to(o, 1, {
        progress: 0,
        ease: Power4.easeIn,
        onUpdate: () => {
          this.scale = o.progress;
        },
        onComplete: () => {
          resolve();
        },
      });
    });
  }
}

function createPlanet(size) {
  const geometry = new THREE.TetrahedronGeometry(size, 2);

  const material = getMaterial(PALLETE.orange, true);
  const materials = [];

  const facesLength = geometry.faces.length;
  for (let i = 0; i < facesLength; i += 1) {
    materials.push(material);
  }

  // randomize vertices
  for (let i = 0; i < geometry.vertices.length; i += 1) {
    const v = geometry.vertices[i];
    v.x += -(DEFAULTS.noise / 2) + (Math.random() * DEFAULTS.noise);
    v.y += -(DEFAULTS.noise / 2) + (Math.random() * DEFAULTS.noise);
    v.z += -(DEFAULTS.noise / 2) + (Math.random() * DEFAULTS.noise);
  }

  for (let i = 0; i < facesLength; i += 1) {
    geometry.faces[i].materialIndex = i;
  }

  const mesh = new THREE.Mesh(geometry, new THREE.MultiMaterial(materials));
  mesh.castShadow = true;
  mesh.receiveShadow = true;

  return mesh;
}

function createWireframe(size) {
  const geometryWireframe = new THREE.TetrahedronGeometry(size, 2);
  const materialWireframe = getMaterial(PALLETE.blue, true, true, 0.25, THREE.AdditiveBlending);

  const mesh = new THREE.Mesh(geometryWireframe, materialWireframe);

  return mesh;
}

function getMaterial(color, wireframe = false, transparent = false, opacity = 1, blending = THREE.NoBlending) {
  const mat = !wireframe ? new THREE.MeshStandardMaterial({
    color,
    shading: THREE.FlatShading,
    wireframe,
    transparent,
    opacity,
    blending,
    side: THREE.DoubleSide,
    emissive: 0x270000,
    roughness: 0.9,
  }) : new THREE.MeshBasicMaterial({
    color,
    shading: THREE.FlatShading,
    wireframe,
    transparent,
    opacity,
    blending,
    side: THREE.DoubleSide,
  });

  return mat;
}



