import * as THREE from 'three';

import {
  v3,
} from './utils';

const PALLETE = {
  basic: 0x1abc9c,
  accent: 0x3498db,
};

export const CAMERA_SPEED = 0.01;

export const FAKE_LOADING_TIME = 0.5; // seconds

export const IMPORT_SCALE = 0.1;

export const MOUSE_CONSTRAINT = 0.8;

export const PATHS = {
    models: '/static/models/',
};

export const POSITIONS = [
  [v3(-0.10, 1.60, -1.40), v3(-4.4, 2.5, -1.8), v3(3, 3.8, 9.3)], // camera, pointA, pointB
  [v3(-2.4, 1.6, -1.40), v3(0.2, 7.2, 5.7), v3(-1.5, 30.2, 1)],
  [v3(-2.4, 1.6, -1.40), v3(0.2, 7.2, 5.7), v3(-1.5, 30.2, 1)],
];

const edgesMat = new THREE.LineBasicMaterial({
  color: PALLETE.basic,
  linewidth: 2,
});

const edgesMatAccent = new THREE.LineBasicMaterial({
  color: PALLETE.accent,
  lineWidth: 4,
});

const wireframeMat = new THREE.MeshBasicMaterial({
  color: PALLETE.basic,
  side: THREE.DoubleSide,
  transparent: true,
  opacity: 0.25,
  wireframe: true,
  blending: THREE.AdditiveBlending,
});

const facesMat = new THREE.MeshBasicMaterial({
  color: PALLETE.basic,
  side: THREE.DoubleSide,
  transparent: true,
  opacity: 0.2,
  // blending: THREE.AdditiveBlending
});

const facesMatAccent = new THREE.MeshBasicMaterial({
  color: PALLETE.accent,
  side: THREE.DoubleSide,
  transparent: true,
  opacity: 0.9,
  blending: THREE.AdditiveBlending,
});

export const MATERIALS = {
    edgesMat,
    edgesMatAccent,
    wireframeMat,
    facesMat,
    facesMatAccent,
};



